import React from "react";
import * as S from "./style";

const SoftwarePage = () => {
  const handleCopyClipBoard = async (text: string) => {
    try {
      await navigator.clipboard.writeText(text);

      alert(" !");
    } catch (error) {
      alert(" !");
    }
  };

  return (
    <>
      <S.MainWrapper>
        <S.Imgwrapper>
          <img src="../result-img/software.png" id="img1" />

          <S.Top>60% </S.Top>
          <S.Mid>30% </S.Mid>
          <S.Bottom>10% </S.Bottom>

          <S.Introduce>    </S.Introduce>

          <S.Home>
            <img src="../result-img/home-white.png" id="img2" />
          </S.Home>

          <S.ShareBtn>
            <img src="../result-img/share-white.png" id="img3" />
          </S.ShareBtn>

          <S.LinkBtn onClick={() => handleCopyClipBoard(" ")}>
            <img src="../result-img/link-white.png" id="img4" />
          </S.LinkBtn>
        </S.Imgwrapper>
      </S.MainWrapper>
    </>
  );
};

export default SoftwarePage;
