import React from "react";
import {Link} from "react-router-dom";
import * as S from "./style";

const WebPage = () => {
  const handleCopyClipBoard = async (text: string) => {
    try {
      await navigator.clipboard.writeText(text);

      alert(" !");
    } catch (error) {
      alert(" !");
    }
  };

  return (
    <>
      <S.MainWrapper>
        <S.Img src="../result-img/web.png" id="img1" />
        <S.Toppercent>60% </S.Toppercent>
        <S.Midpercent>30% </S.Midpercent>
        <S.Bottompercent>10% </S.Bottompercent>
        <S.Developer1></S.Developer1>
        <S.Developer2></S.Developer2>
        <S.Developer3></S.Developer3>
        <S.Developer4></S.Developer4>
        <S.Developer5></S.Developer5>

        <S.Share style={{cursor: "pointer"}}>
          <img src="../result-img/share-white.png" id="img3" />
        </S.Share>

        <S.LinkBtn
          onClick={() => handleCopyClipBoard(" ")}
          style={{cursor: "pointer"}}
        >
          <img src="../result-img/link-white.png" id="img4" />
        </S.LinkBtn>

        <Link to="/" style={{textDecoration: "none"}}>
          <S.Home style={{cursor: "pointer"}}>
            <img src="../result-img/home-white.png" id="img2" />
          </S.Home>
        </Link>
      </S.MainWrapper>
    </>
  );
};

export default WebPage;
